
import Tabbar from '@/components/Public/Tabbar'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
components: {
Tabbar,
Badge,
Group,
Cell,
Swiper,
SwiperItem
},
data: () => ({
list: [],
swiper: [],
itemList: ['推荐', '婚姻家庭', '交通事故', '劳动用工', '治安刑事', '医疗事故', '房产土地', '责权责务', '合同纠纷', '征地拆迁']
}),
mounted () {

this.$nextTick(() => {
this.getList()
this.getSwiper()
})
},
methods: {
getList: function () {
this.$getData(this.$configs.api.issue, '', response => {
this.list = response.list
})
},
getSwiper: function () {
this.$getData(this.$configs.api.issue, `?page=1&pageSize=3&sort=views&order=-1`, response => {
this.swiper = response.list
})
}
}
}



